















import { Component, Vue } from 'vue-property-decorator';
import { CaptainsLogClass } from './model/CaptainsLogClasses';
import LostBattleArray from './model/LostBattleArray';
import ManPwrArray from './model/ManPwrArray';

interface WarriorFileOutput {
  captainsLog: {[index: string]: {[index: number]: string}};
  lostBattleArray: string[];
  manPwrArray: {[index: string]: string};
}

@Component
export default class CaptainsLog extends Vue {
  get captainsLog(): CaptainsLogClass
  {
    return this.$store.state.captainsLog;
  }

  get lostBattleArray(): LostBattleArray
  {
    return this.$store.state.lostBattleArray;
  }

  get manPwrArray(): ManPwrArray
  {
    return this.$store.state.manPwrArray;
  }

  private async onSaveClicked()
  {
    let outputObj: WarriorFileOutput = { 
      captainsLog: await this.captainsLog.getStoredValue(),
      lostBattleArray: await this.lostBattleArray.getStoredValue(), 
      manPwrArray: await this.manPwrArray.getStoredValue() 
    };
    let output = JSON.stringify(outputObj);
    let blob = new Blob([output], {type: 'application/json'});
    let url = URL.createObjectURL(blob);

    let a = document.createElement('a');
    a.href = url;
    a.download = "warrior.warriorApp";
    a.textContent = "Download warrior.warriorApp";
    a.onclick = () => { document.body.removeChild(a); }
    a.style.display = "none";

    document.body.appendChild(a);
    a.click();
  }

  private handleFileSelect(evt) {
    let files = evt.target.files;

    let fileReader = new FileReader();
    fileReader.onload = (fileLoadedEvent: any) => {
      let output: WarriorFileOutput = JSON.parse(fileLoadedEvent.target.result);
      this.captainsLog.setStoredValue(output.captainsLog);
      this.lostBattleArray.setStoredValue(output.lostBattleArray);
      this.manPwrArray.setStoredValue(output.manPwrArray);
    }
    fileReader.readAsText(files[0], "UTF-8");
  }
}
