import localforage from 'localforage';

export class CaptainLogQuestion {
    public question: string = '';
    public anwser: string = '';

    constructor(question: string) {
        this.question = question;
    }

    get isAnwsered(): boolean {
        return this.anwser.trim().length > 0;
    }
}

export const CaptainsLogQuestions = [
    '1. Why are you fighting? Why don’t you just give up?',
    '2. How did you win your most recent difficult battle? ' + 
        'What have you been doing right when you win?',
    '3. Are you keeping the daily MAN/GRL power journal? ' + 
        'What day are you on now?',
    '4. What are you doing for your MAN/GRL goals? ' + 
        'Do you have meaningful rituals in place? Are they sufficient? ' +
        'What is your Flag Pole for drilling to help you achieve your goals? ' + 
        'Have you drilled them at least 3 to 5 times today?',
    '5. When you lost, what technique did the enemy use to defeat you? ' +
        'Is there a pattern? If you could replay the event, what could you have ' + 
        'done to beat him? What drills can you do to make sure you win next time ' +
        'if he tries something similar?',
    '6. Prophesy: What strategy will Satan try in the next week? ' + 
        'What do you need to do to be ready for such an attack?'
];

export class CaptainsLogQuestionArray {
    public questions: CaptainLogQuestion[] = [];
    constructor(anwsers: {[index: number]: string} = {}) {
        CaptainsLogQuestions.forEach(elem => this.questions.push(new CaptainLogQuestion(elem)));

        if(anwsers)
        {
            Object.keys(anwsers).forEach((key) => {
                this.questions[key].anwser = anwsers[key];
            });
        }
    }

    public get anwserDict(): {[index: number]: string}
    {
        let output: {[index: number]: string} = {};
        this.questions.forEach((question, index) => {
            if(question.isAnwsered) {
                output[index] = question.anwser;
            }
        });
        return output;
    }

    public get numberAnwsers(): number
    {
        let output = 0;
        this.questions.forEach((question) => {
            if(question.isAnwsered) { output++; }
        });
        return output;
    }

    public get clipboardContent(): string
    {
        let outputArray: string[] = [];
        for(let i = 0; i < 6; i++) {
            if(this.questions[i].isAnwsered)
            {
                outputArray.push(`${i + 1} - ${this.questions[i].anwser}`);
            }
        }
        let output = '';
        for(let i = 0; i < outputArray.length; i++)
        {
            output += outputArray[i];
            if(i != (outputArray.length - 1)) { output += '\n----------\n' }
        }
        return output;
    }
}

type CaptainsLogType = {[index: string]: {[index: number]: string}};

export class CaptainsLogClass {
    // index is dateStr
    public dict: {[index: string]: CaptainsLogQuestionArray} = {};
    private key: string = 'captainslog';

    async initialize() {
        let value: CaptainsLogType = await localforage.getItem<CaptainsLogType>(this.key) || {};
        Object.keys(value).forEach((dateStr) => {
            this.dict[dateStr] = new CaptainsLogQuestionArray(value[dateStr]);
        });
    }

    async getStoredValue()
    {
        return await localforage.getItem<CaptainsLogType>(this.key) || {};
    }

    async setStoredValue(value: {[index: string]: {[index: number]: string}})
    {
        Object.keys(value).forEach((dateStr) => {
            this.dict[dateStr] = new CaptainsLogQuestionArray(value[dateStr]);
        });
        this.commitChanges();
    }

    get partialDates(): string[] {
        let output: string[] = [];
        for (let dateStr in this.dict)
        {
            let value = this.dict[dateStr];
            if ((value.numberAnwsers > 0) && (value.numberAnwsers < 6))
            {
                output.push(dateStr);
            }
        }
        return output;
    }

    get fullDates(): string[] {
        let output: string[] = [];
        for (let dateStr in this.dict)
        {
            let value = this.dict[dateStr];
            if (value.numberAnwsers === 6)
            {
                output.push(dateStr);
            }
        }
        return output;
    }

    getCaptainLogByDate(dateStr: string)
    {
        if(!this.dict[dateStr])
        {
            this.dict[dateStr] = new CaptainsLogQuestionArray();
        }

        return this.dict[dateStr];
    }

    async commitChanges(): Promise<void>
    {
        let commitOutput: {[index: string]: {[index: number]: string}} = {};
        Object.keys(this.dict).forEach((key) => {
            if(this.dict[key].numberAnwsers > 0)
            {
                commitOutput[key] = this.dict[key].anwserDict;
            }
        });
        await localforage.setItem<CaptainsLogType>(this.key, commitOutput);
    }
}