import Vue from 'vue'
import Vuex from 'vuex'
import { CaptainsLogClass } from './model/CaptainsLogClasses'
import LostBattleArray from './model/LostBattleArray'
import ManPwrArray from './model/ManPwrArray'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    captainsLog: new CaptainsLogClass(),
    lostBattleArray: new LostBattleArray(),
    manPwrArray: new ManPwrArray(),
    initialized: false,
  },
  mutations: {
    setCaptainsLog(state, captainsLog: CaptainsLogClass)
    {
      state.captainsLog = captainsLog;
    },
    setLostBattle(state, lostBattle: LostBattleArray)
    {
      state.lostBattleArray = lostBattle;
    },
    setManPwr(state, manPwr: ManPwrArray)
    {
      state.manPwrArray = manPwr;
    },
    setInitialized(state, value: boolean)
    {
      state.initialized = value;
    }
  },
  actions: {
    async initialize(context) {
      context.commit('setInitialized', false);
      let captainsLog: CaptainsLogClass = new CaptainsLogClass();
      await captainsLog.initialize();
      context.commit('setCaptainsLog', captainsLog);
      let lostBattleArray: LostBattleArray = new LostBattleArray();
      await lostBattleArray.initialize();
      context.commit('setLostBattle', lostBattleArray);
      let manPwrArray: ManPwrArray = new ManPwrArray();
      await manPwrArray.initialize();
      context.commit('setManPwr', manPwrArray);
      context.commit('setInitialized', true);
    }
  }
});
